const config = {
  tokenUrl: 'https://api.weixin.qq.com/cgi-bin/token',
  ticketUrl: 'https://api.weixin.qq.com/cgi-bin/ticket/getticket',
  apiList: ['onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ',
    'onMenuShareWeibo', 'onMenuShareQZone', 'chooseImage', 'uploadImage'],
  logger: console.log,
}

/**
 *
 * @param appId
 * @param appSecret
 * @param domain
 * @param apiList
 */
export const setWxConfig = ({ appId, appSecret, domain, apiList }) => {
  if (!appId || !appSecret || !domain) {
    throw new Error('注意, 必须提供 appId, appSecret, domain 信息!')
  }
  config.appId = appId
  config.appSecret = appSecret
  config.domain = domain
  config.apiList = apiList || config.apiList
}

export const setWxLogger = (logger) => {
  config.logger = logger || config.logger
}

export default config
